import React, { useState, useMemo, useEffect } from 'react';
import moment from 'moment';

import DaysOfWeek from './DaysOfWeek';
import TimeOfDay from './TimeOfDay';
import Cells from './Cells';
import Event from './Event';

import { useTimeOfDay, useDaysOfWeek, hasConflict, hasOverlap } from './util';

import useLocalContext from '@/design/hooks/createContext';
import { DomBoundary } from '@/design/components';

// 
const { Provider, useContext } = useLocalContext();
export const useEventCalendarContext = useContext;

const EventCalendar = ({ events = [], onRangeSelect, onTimeChange }) => {
  // 
  const [isAm, setIsAm] = useState(moment().hour() <= 13);
  // 
  const [hideWeekend, setHideWeek] = useState(true);

  //   
  const {
    startHour,
    startMinute,
    endHour,
    endMinute,
    cellTimeGap,
    cellHeight,
  } = useMemo(() => {
    return {
      startHour: 9, //  
      startMinute: 30, //  
      endHour: 19, //  
      endMinute: 30, //  
      cellTimeGap: 30, // 
      cellHeight: 48,
    };
  }, []);
  // const {
  //   startHour,
  //   startMinute,
  //   endHour,
  //   endMinute,
  //   cellTimeGap,
  //   cellHeight,
  // } = useMemo(() => {
  //   return isAm
  //     ? {
  //         startHour: 9, //  
  //         startMinute: 0, //  
  //         endHour: 12, //  
  //         endMinute: 30, //  
  //         cellTimeGap: 30, // 
  //         cellHeight: 62,
  //       }
  //     : {
  //         startHour: 14, //  
  //         startMinute: 0, //  
  //         endHour: 19, //  
  //         endMinute: 0, //  
  //         cellTimeGap: 30, // 
  //         cellHeight: 62,
  //       };
  // }, [isAm]);

  // 
  const [weekOffset, setWeekOffset] = useState(0);
  const daysOfWeek = useDaysOfWeek(weekOffset, hideWeekend);

  //   
  const timeOfDay = useTimeOfDay(
    { startHour, startMinute, endHour, endMinute, cellTimeGap },
    hideWeekend,
  );

  // 
  const [firstSelect, setFirstSelect] = useState(); // 
  const [secondSelect, setSecondSelect] = useState(); // 

  // 
  const onKeyUp = (e) => {
    if (e.key === 'Escape') {
      setFirstSelect(null);
      setSecondSelect(null);
    }
  };

  useEffect(() => {
    document.addEventListener('keyup', onKeyUp);
    return () => document.removeEventListener('keyup', onKeyUp);
  }, []);

  useEffect(() => {
    // console.log({
    //   startTime: moment(daysOfWeek[0])
    //     .hour(startHour)
    //     .minute(startMinute)
    //     .format('YYYY-MM-DD HH:mm:ss'),
    //   endTime: moment(daysOfWeek[daysOfWeek.length - 1])
    //     .hour(endHour)
    //     .minute(endMinute)
    //     .format('YYYY-MM-DD HH:mm:ss'),
    // });

    // 
    onTimeChange &&
      onTimeChange({
        startTime: moment(daysOfWeek[0])
          .hour(startHour)
          .minute(startMinute)
          .format('YYYY-MM-DD HH:mm:ss'),
        endTime: moment(daysOfWeek[daysOfWeek.length - 1])
          .hour(endHour)
          .minute(endMinute)
          .format('YYYY-MM-DD HH:mm:ss'),
      });
  }, [isAm, weekOffset]);

  //  cell
  const onEnterCell = (m) => {
    // console.log('onMouseEnter', m.format('MM-DD HH:mm'));

    const conflict = hasConflict(events, m);

    if (conflict) {
      console.log('conflict');
      if (secondSelect) setSecondSelect(null);
      return;
    }

    // 
    if (firstSelect) {
      // 
      if (firstSelect.isSame(m, 'day')) {
        setSecondSelect(m);
      }
      //  
      else {
        setSecondSelect(null);
      }
    }
  };

  //  cell
  const onClickCell = (m) => {
    // console.log('onClick', m.format('MM-DD HH:mm'));

    const conflict = hasConflict(events, m);

    if (conflict) return;

    // 
    if (firstSelect && secondSelect) {
      const [start, end] = firstSelect.isBefore(secondSelect)
        ? [firstSelect, moment(secondSelect).add(cellTimeGap, 'minute')]
        : [secondSelect, moment(firstSelect).add(cellTimeGap, 'minute')];

      const overlap = hasOverlap(events, [start, end]);
      if (overlap) {
        // 
        setFirstSelect(m);
        setSecondSelect(null);
      } else {
        // 
        onRangeSelect([start, end]);

        // 
        setFirstSelect(null);
        setSecondSelect(null);
      }
    }
    // 
    else {
      setFirstSelect(m);
      setSecondSelect(m);
    }
  };

  return (
    <Provider
      value={{
        // 
        weekOffset,
        setWeekOffset,
        daysOfWeek,

        // 
        timeOfDay,

        // 
        isAm,
        setIsAm,

        // 
        startHour,
        startMinute,
        endHour,
        endMinute,
        cellTimeGap,
        cellHeight,
        hideWeekend,

        // 
        firstSelect,
        secondSelect,

        onEnterCell,
        onClickCell,

        // 
        events,
      }}
    >
      <DomBoundary
        onClickOutside={() => {
          // 
          setFirstSelect(null);
          setSecondSelect(null);
        }}
      >
        <div
          className="EventCalendar"
          style={{
            display: 'grid',
            gridTemplateColumns: '64px 1fr',
            gridTemplateRows: '64px 1fr',
            gridTemplateAreas: `
                ". week"
                "time cells"
            `,
          }}
        >
          {/*  */}
          <div style={{ gridArea: 'week' }}>
            <DaysOfWeek />
          </div>

          {/*  */}
          <div style={{ gridArea: 'time' }}>
            <TimeOfDay />
          </div>

          <div
            style={{
              gridArea: 'cells',
              position: 'relative',
              overflow: 'hidden',
              display: 'grid', //  safari
            }}
          >
            {/*  */}
            <Cells />

            {/*  */}
            {events.map(({ key, startTime, endTime, content }) => (
              <Event key={key} range={[moment(startTime), moment(endTime)]}>
                {content}
              </Event>
            ))}
          </div>
        </div>
      </DomBoundary>
    </Provider>
  );
};

export default EventCalendar;
